  font-family: "Open Sans", "Arial", sans-serif;
  color: #000000;
  background-color: #f30460;
  cursor: pointer;
  font-size: 12px;
  line-height: 12px;
  font-weight: 400;
  position: relative;
  align-self: flex-end;
  grid-area: button;
  padding: 10px;
  margin-bottom: 10px;
  background-color: #f30460;
  border-radius: 50%;
  width: 32px;
  height: 32px; }
  .form__close::before {
    position: absolute;
    content: "";
    width: 20px;
    height: 2px;
    top: 50%;
    left: 50%;
    background-color: #ffffff;
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
            transform: translate(-50%, -50%) rotate(45deg);
    -webkit-box-shadow: none;
            box-shadow: none; }
  .form__close::after {
    position: absolute;
    content: "";
    width: 2px;
    height: 20px;
    top: 50%;
    left: 50%;
    background-color: #ffffff;
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
            transform: translate(-50%, -50%) rotate(45deg); }
  .form__close:hover {
