@-webkit-keyframes buzz {
  0%,
  100% {
    -webkit-transform: translateX(0);
            transform: translateX(0); }
  10%,
  30%,
  50%,
  70%,
  90% {
    -webkit-transform: translateX(-4px);
            transform: translateX(-4px); }
  20%,
  40%,
  60%,
  80% {
    -webkit-transform: translateX(4px);
            transform: translateX(4px); } }

@keyframes buzz {
